import React, {useState, useEffect} from 'react';
import axios from 'axios';
import NewsItem from './NewsItem';
import styled from 'styled-components';
import { AiOutlineLoading3Quarters } from "react-icons/ai";

const NewsSectionBlock = styled.div`
    .row {padding-top : 80px; display: flex; flex-wrap:wrap;font-size:13px;}
    .loadWrap {min-height:1000px; text-align:center; line-height: 1000px;
        .loadicon {font-size:150px;
            animation : loadspin infinite 5s linear;
        }
        @keyframes loadspin {
            from {transfrom : rotate(0deg);}
            to { transform :rotate(3turn);}
        }
    }
`;

const NewsSection = ({category}) => {
    const [articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);

    const getNews = async () => {
        // setLoading(true)
        const query = category === 'all' ? '' : `&category=${category}`;
        const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=f5c57a809173402f83afeeea072612c9`);
        // &category=내용 (떨어지면 안된다)
        console.log(response);
        setArticles(response.data.articles);
        setLoading(true);
    }
    useEffect(
        () => {
            getNews()
        }, [category]
    )

    if (!loading) {
        return (
            <NewsSectionBlock>
                <div className="loadWrap">
                    <AiOutlineLoading3Quarters className="loadicon" />
                </div>
            </NewsSectionBlock>
            )
    } else {
        if (!articles) {
            return <NewsSectionBlock></NewsSectionBlock>
        } else {
            return (
                <NewsSectionBlock>
                    <div className="row">
                        {
                            articles.map( (article) => ( <NewsItem key={article.url} article={article} />))
                        }
                    </div>
                </NewsSectionBlock>
            );
        }
    }

};

export default NewsSection;